.select_mate {
  position: relative;
  width: 300px;
  min-height: 60px;
  font-family: "ProximaNovaRegular", Arial, sans-serif;
  color: #1c1c1c;
  background-color: #fff;
  transition: all 275ms ease-in-out;
}

.select_mate select {
  position: absolute;
  overflow: hidden;
  height: 0px;
  opacity: 0;
  z-index: -1;
}

.cont_list_select_mate {
  position: relative;
  float: left;
  width: 100%;
}

.cont_select_int {
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 999;
  overflow: hidden;
  height: 0px;
  width: 100%;
  background-color: #fff;
  padding: 0px;
  margin-bottom: 0px;
  margin-top: 0px;
  border-radius: 0px 0px 3px 3px;
  box-shadow: 1px 4px 10px -2px rgba(0, 0, 0, 0.2);
  transition: all 275ms ease-in-out;
}

.cont_select_int li {
  position: relative;
  float: left;
  width: 100%;
  border-top: 1px solid #E0E0E0;
  background-color: #ffffff;
  list-style-type: none;
  padding: 10px 2% 10px 24px;
  margin: 0px;
  transition: all 275ms ease-in-out;
  display: block;
  cursor: pointer;
}

.cont_select_int li:last-child {
  border-radius: 3px;
  border-bottom: 0px;
}

.cont_select_int li:hover {
  background-color: #f5f6f6;
}

.cont_select_int .active {
  background-color: #f5f6f6;
}

.option__choosed {
  padding: 20px 2% 20px 24px;
  width: 96%;
  display: block;
  margin: 0px;
  cursor: pointer;
}

.icon_select_mate {
  position: absolute;
  top: 0px;
  bottom: 0;
  margin: auto;
  right: 20px;
  transition: all 275ms;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 8px 4.5px 0 4.5px;
  border-color: #898989 transparent transparent transparent;
}

.accordion {
  border-top: solid 1px #e1e1e1;
}

.accordion li {
  border-bottom: solid 1px #e1e1e1;
  position: relative;
}

.accordion li p {
  display: none;
  margin-bottom: 10px;
  padding: 0 0 10px 18px;
  font-family: "ProximaNovaLight", Arial, sans-serif;
  font-size: 16px;
  color: #3f3f3f;
}

.accordion__tab {
  width: 100%;
  display: block;
  padding: 18px 40px 18px 0;
  cursor: pointer;
  font-weight: 600;
  font-size: 18px;
  font-family: "ProximaNovaSemibold", Arial, sans-serif;
  user-select: none;
}

.accordion__tab:after {
  content: "";
  width: 9px;
  height: 9px;
  border-right: 2px solid #90c800;
  border-bottom: 2px solid #90c800;
  position: absolute;
  right: 25px;
  top: 27px;
  transform: rotate(-45deg);
  transition: all .2s ease-in-out;
}

.accordion__tab.active:after {
  transform: rotate(45deg);
  transition: all .2s ease-in-out;
}

.dd-select {
  position: relative;
  width: 100%;
  font-family: "ProximaNovaRegular", Arial, sans-serif;
  font-size: 18px;
  color: #1c1c1c;
  background-color: #fff;
  border-bottom: solid 1px #ccc;
  cursor: pointer;
}

.dd-select label {
  margin: 0 !important;
}

.dd-desc {
  display: block;
  overflow: hidden;
  margin-top: -3px;
  font-family: "ProximaNovaLight", Arial, sans-serif;
  font-size: 12px;
  font-weight: 300;
  line-height: 1;
  color: #959595;
}

.dd-selected {
  height: 70px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 0 2% 0 24px;
}

.dd-pointer {
  position: absolute;
  top: 0px;
  bottom: 0;
  margin: auto;
  right: 20px;
  transition: all 275ms;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 8px 4.5px 0 4.5px;
  border-color: #898989 transparent transparent transparent;
}

.dd-pointer-up {
  transform: rotate(180deg);
}

.dd-options {
  list-style: none;
  display: none;
  position: absolute;
  z-index: 2000;
  margin: 0;
  padding: 0;
  background: #fff;
  overflow: auto;
}

.dd-option {
  padding: 10px 10px 10px 24px;
  display: block;
  font-size: 16px;
  border-bottom: solid 1px #E0E0E0;
  overflow: hidden;
  text-decoration: none;
  cursor: pointer;
  -webkit-transition: all 0.05s ease;
  -moz-transition: all 0.05s ease;
  -o-transition: all 0.05s ease;
  -ms-transition: all 0.05s ease;
}

.dd-options > li:last-child > .dd-option {
  border-bottom: none;
}

.dd-option:hover {
  color: #3d77ea;
  background: #f5f6f6;
}

.dd-selected-description-truncated {
  text-overflow: ellipsis;
  white-space: nowrap;
}

.dd-option-selected {
  background: #f5f6f6;
}

.dd-option-image,
.dd-selected-image {
  vertical-align: middle;
  float: left;
  margin-right: 5px;
  max-width: 64px;
}

.dd-image-right {
  float: right;
  margin-right: 15px;
  margin-left: 5px;
}

.dd-container {
  position: relative;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root * {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;
}

html,
